/* eslint-disable */
import React, {useState, useContext, lazy, Suspense} from 'react';
import { Navbar, Container, Nav, NavDropdown, Form, FormControl, Button } from 'react-bootstrap';
import './App.css';
import Data from './data.js';
// import Detail from './Detail.js'
let Detail = lazy(()=> import('./Detail.js'));

import axios from 'axios';
import Cart from './cart/Cart.js';

import { Link, Route, Switch, useHistory } from 'react-router-dom';

export let stockContext = React.createContext();

function App() {

  let[shoes, shoesModify] = useState(Data);
  let[loding, lodingModify] = useState(true);
  let [stock, stockModify] = useState([10,11,12]);

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Container fluid>
          <Navbar.Brand href="#">ShoeShop</Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarScroll" />
          <Navbar.Collapse id="navbarScroll">
          <Nav
            className="me-auto my-2 my-lg-0"
            style={{ maxHeight: '100px' }}
            navbarScroll
          >
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link as={Link} to="/detail">Detail</Nav.Link>
              <NavDropdown title="Link" id="navbarScrollingDropdown">
                <NavDropdown.Item href="#action3">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action4">Another action</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action5">
                  Something else here
                </NavDropdown.Item>
              </NavDropdown>
            <Nav.Link href="#" disabled>
                Link
            </Nav.Link>
          </Nav>
          <Form className="d-flex">
            <FormControl
              type="search"
              placeholder="Search"
              className="me-2"
              aria-label="Search"
            />
            <Button variant="outline-success">Search</Button>
          </Form>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      
    <Switch>

      <Route exact path="/">
        <Jumbotron></Jumbotron>
          <div className="container">

            <stockContext.Provider value={ stock }>

            <div className="row">
              {
                shoes.map((a,i)=>{
                  return <Card shoes={a} i={i} key={i}></Card> // a = shoes[i]
                })
              }
            </div>

            </stockContext.Provider>

            <button className="btn btn-outline-primary" onClick={()=>{

              // axios.post('serverURL', { id : 'coding', pw : 1234 }).then();

              axios.get('https://codingapple1.github.io/shop/data2.json')
              .then((result)=>{
                console.log(result.data);
                shoesModify([ ...shoes, ...result.data ]);

              })//성공시
              .catch(()=>{
                console.log('실패');
              })//실패시;

            }}>더보기</button>
          </div>
      </Route>
      <Route path="/detail/:id">
      <stockContext.Provider value={ stock }>
        <Suspense fallback={<div>로딩중</div>}>
          <Detail shoes={shoes} stock={stock} stockModify={stockModify} />
        </Suspense>
      </stockContext.Provider>
      </Route>

      <Route path="/cart">
        <Cart />
      </Route>

    

      {/* <Route path="/" component={Modal}></Route> */}
        
    </Switch>

    </div>
  );
}
  
  function Card(props) {

    let stock = useContext(stockContext);
    let history = useHistory();

    return (
      <div className="col-md-4" onClick={()=>{ history.push('/detail/'+props.shoes.id)}}>
          <img src={'https://codingapple1.github.io/shop/shoes'+ (props.i+1) +'.jpg'} width="100%" />
          <h4>{ props.shoes.title }</h4>
          <p>{ props.shoes.content }</p>
          <p>{ props.shoes.price } 원</p>
          <Test></Test>
      </div>
    )
  }

  function Jumbotron() {
    return (
      <div className="jumbotron">
        <h1>20% Season Off</h1>
        <p>this is a simple hero unit, a simple jumbotron</p>
        <p>
          <Button variant="primary">Learn more</Button>
        </p>
      </div>
    )
  }

  function Test() {
    let stock = useContext(stockContext);
    return (
      <p>재고 : {stock[0]}</p>
    )
  }


export default App;
